<template>
  <div class="hello">
      <div class="hello">
        <el-container style="height: 100%; border: 0px solid #eee">
          <el-aside width="350px" style="position: fixed">
            <clock />
            <el-row>
              <el-button type="primary" round @click="handle1">1.JVM</el-button>
              <el-button type="success" round @click="handle2">2.数据结构</el-button>
              <el-button type="info" round @click="handle3">3.数据库</el-button>
              <el-button type="warning" round @click="handle4">4.网络</el-button>
              <el-button type="danger" round @click="handle5">5.面试金典</el-button>
              <el-button style="border-color:#fcb70a;" round @click="handle6">6.算法导论</el-button>
              <el-button type="primary" round @click="handle7">7.Java</el-button>
              <el-button type="success" round @click="handle8">8.ssm</el-button>
              <el-button type="info" round @click="handle9">9.操作系统</el-button>
              <el-button type="warning" round @click="handle10">10.其他</el-button>
              <el-button type="danger" round @click="handle11">11.面试题</el-button>
              <el-button style="border-color:#fcb70a;" round @click="handle12">12.书单</el-button>
            </el-row>
          </el-aside>

          <el-container>
            <el-main>

              <div class="htmlContent" style="text-align: left; margin-left: 20px; overflow-y:auto; height: 100%" v-html="htmlContent">abc</div>
            </el-main>
          </el-container>
        </el-container>
      </div>
  </div>
</template>

<script>
import Clock from './Clock'
import axios from 'axios';

export default {
  name: 'Interview',
  components: {
        "clock": Clock
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      htmlContent: ""
    }
  },
  mounted() {
    this.handle1()
  },
  methods: {
    async handle1() {
        await axios.get("/static/interview/1.md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    async handle2() {
        await axios.get("/static/interview/2.md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    async handle3() {
       await axios.get("/static/interview/3.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    },
    async handle4() {
       await axios.get("/static/interview/4.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    },
    async handle5() {
       await axios.get("/static/interview/5.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    },
    async handle6() {
       await axios.get("/static/interview/6.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    },
    async handle7() {
       await axios.get("/static/interview/7.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    },
    async handle8() {
       await axios.get("/static/interview/8.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    },
    async handle9() {
       await axios.get("/static/interview/9.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    },
    async handle10() {
       await axios.get("/static/interview/10.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    },
    async handle11() {
      await axios.get("/static/interview/11.md").then(res =>{
          this.htmlContent = this.converter.makeHtml(res.data);
      })
    },
    async handle12() {
       await axios.get("/static/interview/12.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    }
  }
}
</script>

<style scoped>
  .hello {
    background-color: #c7edcc;
    height: 95%;
    width: 100%;
    position: fixed;
  }

  .el-aside {
    border-color:#98bf21;
    border-right-style:solid;
    line-height: 50px;
  }

  .el-container {
    height: 100%;
    background-color: #c7edcc;
  }

  .el-main {
    overflow-y:auto;
    margin-left: 350px;
    height: 100%;
  }

  .el-row {
    margin-top: 10px;
  }

  .el-button {
    width: 130px;
    margin-left: 10px;
  }

  .clock {
    float: center;
  }

  .htmlContent {
    background-color: #c7edcc;
    height: 100%;
    font-size: 18px;
    font-family: 微软雅黑;
  }

  .el-header {
    position: fixed;
    background-color: #c7edcc;
    color: #333;
    text-align: left;
    line-height: 30px;
    width: 350px;
  }

  a {
    text-decoration: none;
    color: #dd00dd;
    font-size: 130%;
    #font-weight: bold;
  }

</style>



