<template>
  <div class="hello">
      <div class="hello">
        <el-container style="height: 100%; border: 0px solid #eee">
          <el-aside width="350px" style="position: fixed">
              <clock />
              <el-tag type="success" style="width: 100%; height: 30px; font-size: 15px; margin-top: 30px;">
                  当前第 {{ curId }} 页
              </el-tag>

              <el-row>
                    <el-button type="success" round @click="handlePre">前一页</el-button>
                    <el-button type="danger" round @click="handleNext">下一页</el-button>
              </el-row>
          </el-aside>
          <el-container>
            <el-main>
              <div class="htmlContent" style="text-align: left; margin-left: 50px; overflow-y:auto; height: 90%" v-html="htmlContent">abc</div>
            </el-main>
          </el-container>
        </el-container>
      </div>
  </div>
</template>

<script>
import Clock from './Clock'
import axios from 'axios';

export default {
  name: 'Script',
  components: {
        "clock": Clock
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      htmlContent: "",
      curId: 0,
      allId: 0,
    }
  },
  mounted() {
    this.handle1_1()
    console.log(this.$route.params.id)

  },
  methods: {
    async handle1_1() {
        var dir = this.$route.params.dir
        var id = this.$route.params.id
        this.curId = parseInt(id)
        await axios.get("/static/knowledge/" + dir + "/" + id + ".md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    async handleNext() {
        var dir = this.$route.params.dir
        this.curId = this.curId + 1
        var id = this.curId
        await axios.get("/static/knowledge/" + dir + "/" + id + ".md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    async handlePre() {
        var dir = this.$route.params.dir
        this.curId = this.curId - 1
        var id = this.curId
        await axios.get("/static/knowledge/" + dir + "/" + id + ".md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    }
  }
}
</script>

<style scoped>
  .hello {
    height: 95%;
    width: 100%;
    position: fixed;
    background-image: linear-gradient(#c7edcc, #54FF9F);

  }

  .el-aside {
    border-color:#98bf21;
    border-right-style:solid;
    line-height: 50px;
  }

  .el-container {
    height: 100%;
  }

  .el-main {
    overflow-y:auto;
    height: 100%;
    margin-left: 300px;
  }
</style>



