<template>
  <div class="hello">
      <div class="hello">
        <el-container style="height: 100%; border: 0px solid #eee">
          <el-aside width="400px" style="position: fixed">
            <h1 style="color: black; font-family: 楷体">{{ this.titleMap[this.dir] }} - {{ title }}</h1>
            <el-button :type="getButtonType(i)" @click="handle(directoryList[i - 1][0]);updateTitle(i - 1);" v-for="i in directoryList.length">
               {{ directoryList[i - 1][1] }}
            </el-button>
          </el-aside>

          <el-container>
            <el-main>

                <div class="htmlContent" style="text-align: left; margin-left: 20px; overflow-y:auto; height: 100%" v-html="htmlContent">
                </div>
            </el-main>
          </el-container>
        </el-container>
      </div>
  </div>
</template>

<script>
import axios from 'axios';
import Clock from './Clock'
import Windmill from './Windmill';

export default {
  name: 'Word',
  components: {
    "clock": Clock,
    "windmill": Windmill
  },
  data () {
    return {
      htmlContent: "",
      dir: "",
      directoryList: [],
      titleMap : {
        "friends3-1": "老友记 - 第三季",
        "friends4-1": "老友记 - 第四季",
        "friends5-1": "老友记 - 第五季",
        "friends5-2": "老友记 - 第五季",
        "him1": "浪漫史 - 第一季",
        "algorithm": "算法导论",
      },
      title : ""
    }
  },
  mounted() {
      var dir = this.$route.params.dir
      this.dir = dir;
      this.getMeta(dir);
      this.handle(this.directoryList[0][0]);
      this.updateTitle(0);
  },
  watch : {
    $route() {
      var dir = this.$route.params.dir
      this.dir = dir;
      this.getMeta(dir);
      this.handle(this.directoryList[0][0]);
      this.updateTitle(0);
    }
  },
  methods: {
    async getMeta(directory) {
      this.directoryList = []
      // 同步请求
      const xhr = new XMLHttpRequest();
      xhr.open('GET', '/static/word/' + directory + '/meta', false);
      xhr.send(null);

      if(xhr.readyState === 4 && xhr.status === 200) {
        console.log(xhr.responseText);

        var list = xhr.responseText.split("\n")
        for(var i = 0; i <= list.length - 2; i++) {
            var arr = [list[i].split(",")[0], list[i].split(",")[1]]
            this.directoryList.push(arr)
        }
      }
    },
    async handle(i) {
        var dir = this.$route.params.dir
        await axios.get("/static/word/" + dir + "/" +  i + ".md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    updateTitle(i) {
      this.title = this.directoryList[i][1]
    },
    getButtonType(index) {
      var mod = index % 5;
      if(mod === 0) {
        return 'primary';
      } else if(mod === 1) {
        return 'success';
      } else if(mod === 2) {

        return 'info';
      } else if(mod === 3) {
        return 'warning';
      } else if(mod === 4) {
        return 'danger';
      }
      return '';
    },

  }
}
</script>

<style scoped>
  .hello {
    background-color: #c7edcc;
    height: 95%;
    width: 100%;
    position: fixed;
  }

  .el-aside {
    border-color:#98bf21;
    line-height: 50px;
  }

  .el-container {
    height: 100%;
    background-color: #c7edcc;
  }

  .el-main {
    overflow-y:auto;
    margin-left: 400px;
    height: 100%;
  }

  .el-row {
    margin-top: 10px;
  }

  .el-button {
    width: 120px;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 15px;
    margin: 12px;
  }

  .clock {
    float: center;
  }

  .htmlContent {
    background-color: #c7edcc;
    height: 100%;
    font-size: 18px;
  }

  .el-header {
    position: fixed;
    background-color: #c7edcc;
    color: #333;
    text-align: left;
    line-height: 30px;
    width: 350px;
  }

  a {
    text-decoration: none;
    color: #dd00dd;
    font-size: 130%;
    #font-weight: bold;
  }


</style>



