<template>
  <div>
    <canvas id="windmill" height="180px" width="180px" />
  </div>

</template>

<script>

export default {
  directives: {  },
  filters: {
  },
  data() {
    return {

    }
  },
  created() {
  },
  mounted() {
    this.getClock()
  },
  methods: {

    getClock() {
      var dom = document.getElementById('windmill')
      var ctx = dom.getContext('2d')
      var width = ctx.canvas.width
      var height = ctx.canvas.height
      var r = width / 2
      var rem = width / 200

      function drawBackground() {
        ctx.save()
        ctx.translate(r, r)
        ctx.beginPath()
        ctx.lineWidth = 100 * rem
        ctx.strokeStyle = 'white' // 边框
        ctx.arc(0, 0, r - ctx.lineWidth / 2, 0, 2 * Math.PI, false)
        ctx.stroke()
        ctx.strokeStyle = 'blue' // 黑色
      }

      function drawHour(second, color) {
        ctx.save()
        ctx.beginPath()

        var rad = 2 * Math.PI / 60 * second * 5
        ctx.rotate(rad)
        ctx.lineWidth = 18 * rem
        ctx.lineCap = 'round'
        ctx.moveTo(0, 0)
        ctx.lineTo(0, -r / 2 * 1.3)
        ctx.stroke()
        ctx.restore()
        ctx.strokeStyle = color
      }

      function drawDot() {
        ctx.beginPath()
        ctx.fillStyle = '#fff'
        ctx.arc(0, 0, 8 * rem, 0, 2 * Math.PI, false)
        ctx.fill()
      }

      function draw() {
        ctx.clearRect(0, 0, width, height)
        var now = new Date()
        var hour = now.getHours()
        var minute = now.getMinutes()
        var second = now.getSeconds()
        var milli = now.getMilliseconds()
        second = second + milli / 1000
        drawBackground()
        drawHour(second, 'red')
        drawHour(second + 20, '#8b00ff')
        drawHour(second + 40, '#00ff00')
        drawDot()
        ctx.restore()
      }
      draw()
      setInterval(draw, 10)
    }

  }
}
</script>

<style scoped>

  div {
    text-align: center;
    margin-top: 30px;
  }
  #windmill {
    border: 0px solid #000000;
    margin-right: 20px;
    float: center;
  }
</style>



