<template>
  <div class="hello">
      <div class="hello">
        <el-container style="height: 100%; border: 0px solid #eee">
          <el-aside width="350px" style="position: fixed">
            <clock />
            <el-row>
              <el-button type="primary" @click="handle1">1.生活大爆炸</el-button>
              <el-button type="success" @click="handle2">2.老爸老妈浪漫史</el-button>
              <el-button type="info" @click="handle3">3.the coupling</el-button>
              <el-button type="warning" @click="handle4">4.算法</el-button>
              <el-button type="danger" @click="handle5">5.好汉两个半</el-button>
              <el-button style="border-color:#fcb70a;" @click="handle6">6.yes minister</el-button>

              <el-button type="primary" @click="handle7">7.老友记</el-button>
              <el-button type="success" @click="handle8">8.设计模式</el-button>
              <el-button type="info" @click="handle9">9.算法导论MIT</el-button>
              <el-button type="warning" @click="handle10">10.其他</el-button>
            </el-row>
          </el-aside>

          <el-container>
            <el-main>

              <div class="htmlContent" style="text-align: left; margin-left: 20px; overflow-y:auto; height: 100%" v-html="htmlContent">abc</div>
            </el-main>
          </el-container>
        </el-container>
      </div>
  </div>
</template>

<script>
import Clock from './Clock'
import axios from 'axios';

export default {
  name: 'Interview',
  components: {
        "clock": Clock
  },
  data () {
    return {
      htmlContent: ""
    }
  },
  mounted() {
    this.handle1()
  },
  methods: {
    async handle1() {
        await axios.get("/static/words/1.md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    async handle2() {
        await axios.get("/static/words/2.md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    async handle3() {
       await axios.get("/static/words/3.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    },
    async handle4() {
       await axios.get("/static/words/4.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    },
    async handle5() {
       await axios.get("/static/words/5.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    },
    async handle6() {
       await axios.get("/static/words/6.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    },
    async handle7() {
       await axios.get("/static/words/7.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    },
    async handle8() {
       await axios.get("/static/words/8.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    },
    async handle9() {
       await axios.get("/static/words/9.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    },
    async handle10() {
       await axios.get("/static/words/10.md").then(res =>{
           this.htmlContent = this.converter.makeHtml(res.data);
       })
    }
  }
}
</script>

<style scoped>
  .hello {
    background-color: #c7edcc;
    height: 95%;
    width: 100%;
    position: fixed;
  }

  .el-aside {
    border-color:#98bf21;
    border-right-style:solid;
    line-height: 50px;
  }

  .el-container {
    height: 100%;
    background-color: #c7edcc;
  }

  .el-main {
    overflow-y:auto;
    margin-left: 350px;
    height: 100%;
  }

  .el-row {
    margin-top: 10px;
  }

  .el-button {
    width: 150px;
    margin-left: 10px;
  }

  .clock {
    float: center;
  }

  .htmlContent {
    background-color: #c7edcc;
    height: 100%;
    font-size: 18px;
  }

  .el-header {
    position: fixed;
    background-color: #c7edcc;
    color: #333;
    text-align: left;
    line-height: 30px;
    width: 350px;
  }

  a {
    text-decoration: none;
    color: #dd00dd;
    font-size: 130%;
    #font-weight: bold;
  }

</style>



