<template>
  <div class="hello">
      <div class="hello">
        <el-container style="height: 100%; border: 0px solid #eee">
          <el-container>
            <el-main>
              <div class="htmlContent" style="text-align: left; margin-left: 50px; overflow-y:auto; height: 100%" v-html="htmlContent">abc</div>
            </el-main>
          </el-container>
        </el-container>
      </div>
  </div>
</template>

<script>
import Clock from './Clock'
import axios from 'axios';

export default {
  name: 'Script',
  components: {
        "clock": Clock
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      htmlContent: ""
    }
  },
  mounted() {
    this.handle1_1()
    console.log(this.$route.params.id)

  },
  methods: {
    async handle1_1() {
        var id = this.$route.params.id
        await axios.get("/static/note/" + id + ".md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    }
  }
}
</script>

<style scoped>
  .hello {
    height: 95%;
    width: 100%;
    position: fixed;
    background-image: linear-gradient(#c7edcc);

  }

  .el-aside {
    border-color:#98bf21;
    border-right-style:solid;
    line-height: 50px;
  }

  .el-container {
    height: 100%;
  }

  .el-main {
    overflow-y:auto;
    height: 100%;

  }
</style>



