<template>
  <el-container style="height: 100%; border: 0px solid #eee">
    <el-main>
      <div class="content" style="text-align: left; margin-left: 50px; overflow-y:auto; height: 90%" v-html="content">abc</div>

    </el-main>
  </el-container>
</template>

<script>
  import axios from 'axios';
  export default {
    name: 'Script',
    components: {
    },
    data () {
      return {
        content: ""
      }
    },
    mounted() {
      this.load()
    },
    methods: {
      async load() {
          var dir = this.$route.params.dir
          var mark = this.$route.params.id
          await axios.get("/static/transcript/" + dir + "/" + mark + ".md").then(res =>{
              this.content = this.converter.makeHtml(res.data);
          })
      },
    }

  }
</script>

<style scoped>
  .el-container {
    height: 100%;
    width: 100%;
    position: fixed;
    overflow: auto;
    background-image: linear-gradient(#c7edcc);
  }

  .el-main {
    overflow-y:auto;
    margin-top: 0px;
    margin-left: 0px;
    height: 100%;
  }

  .content {
    font-size: 30px;
  }
</style>



