<template>
  <div class="hello">
    <el-container style="height: 100%; border: 0px solid #eee">
      <el-aside width="350px" style="position: fixed">
        <windmill />
        <br />

      </el-aside>

      <el-container>
        <el-main>

          <div class="detail-container">

            <div class="filter-container">
              <el-input
                v-model="searchWord"
                placeholder=""
                style="width: 300px"
                class="filter-item"
                @clear="handleFilter"
                @keyup.enter.native="handleFilter"
                @click="handleFilter"
              />

              <el-button
                id = 'searchButton'
                v-waves
                class="filter-item"
                type="primary"
                icon="el-icon-search"
                style="margin-left: 10px"
                @click="handleFilter"
              >查询
              </el-button>
            </div>

            <br />
            <br />
            <el-table
              :key="tableKey"
              v-loading="listLoading"
              :data="list"

              fit
              highlight-current-row
              :show-header="false"
              style="width: 90%; height: 70%; margin:0 auto; line-height: 17px;"
              :row-class-name="tableRowClassName"
              :row-style="{height: '0'}"
              :cell-style="{padding: '8px'}"
            >
              <el-table-column
                label="ID"
                prop="id"
                align="center"
                width=""
                min-width="20"
              />
              <el-table-column label="标题" prop="title" width="" min-width="160" align="center" />
              <el-table-column label="日期" prop="date" width="" min-width="35" align="center" />
              <el-table-column
                label="操作"
                width=""
                min-width="35"

                align="center"
              >
                <template slot-scope="{ row }">
                  <el-button size="mini"  align="center"
                      :type="getButtonType(row)"
                      @click="readDetail(row)">detail</el-button>
                </template>
              </el-table-column>
            </el-table>
            <h1></h1>
            <el-pagination
              background
              layout="prev, pager, next"
              :total="total"
              :current-page.sync="listQuery.page"
              :page-size="10"
              @current-change="handleCurrentChange"
              >
            </el-pagination>
          </div>


        </el-main>
      </el-container>
    </el-container>
  </div>
</template>



<script>
import Windmill from './Windmill';
import Paper from './Paper';
import axios from 'axios';

export default {
  name: 'Script',
  components: {
        "windmill": Windmill,
        "paper": Paper
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      htmlContent: "abcd",
      dialogVisible: false,

      tableKey: 0,
      listLoading: true,
      list: [],
      total: 0,

      listQuery: {},
      showCover: false,
      categoryList: [],

      directoryList: [],
      searchWord: ''

    }
  },
  mounted() {
    var dir = this.$route.params.dir
    this.getDict(dir)
    this.handleFilter(1, 10)
  },
  watch : {
    $route() {
      var dir = this.$route.params.dir
      this.getDict(dir)
      this.handleFilter(1, 10)
    }
  },
  methods: {

    tableRowClassName ({ row, rowIndex }) {
        row.rowIndex = rowIndex;
    },
    getButtonType(row) {
      console.log(row.rowIndex);
      var index = row.rowIndex;
      var mod = index % 5;
      if(mod === 0) {
        return 'primary';
      } else if(mod === 1) {
        return 'success';
      } else if(mod === 2) {

        return 'info';
      } else if(mod === 3) {
        return 'warning';
      } else if(mod === 4) {
        return 'danger';
      }
      return '';
    },
    readDetail(row) {
      var dir = this.$route.params.dir
      // this.$router.push(`/markdown/${dir}/${row.id}`)
      var dir = `markdown/${dir}/${row.id}`
      const routeData = this.$router.resolve({
            name: dir
          });
      window.open(routeData.href + dir, '_blank');

    },
    handleFilter(pageNum, pageSize) {
      pageNum = this.listQuery.page
      pageSize = 10
      if(pageNum == NaN) {
        pageNum = 1;
        pageSize = 10;
      }
      var searchWord = this.searchWord
      searchWord = searchWord.replace(/^\s*|\s*$/g,"");

      // 更具搜索词过滤
      var directoryList = new Array()
      for(var i = 0; i <= this.directoryList.length - 1; i++) {
          console.log(this.directoryList[i][1])
          console.log(this.directoryList[i][1].search(searchWord))
          if(this.directoryList[i][1].search(searchWord) > -1) {
            directoryList.push(this.directoryList[i])
          }
      }

      var start = (pageNum - 1) * pageSize
      var end = start + pageSize - 1

      var list = new Array()
      if(end >= directoryList.length - 1) {
        end = directoryList.length - 1
      }


      for(var i = start; i <= end; i++) {
        list.push({id: directoryList[i][0], title: directoryList[i][1], date: directoryList[i][2] })
      }
      const currentCount = list.length

      list.sort(
        (pre, cur) =>
          pre.id - cur.id
      )
      this.list = list
      this.total = directoryList.length
      this.listLoading = false
    },
    async getDict(directory) {
      this.directoryList = []
      // 同步请求
      const xhr = new XMLHttpRequest();
      xhr.open('GET', '/static/knowledge/' + directory + '/meta', false);
      xhr.send(null);

      if(xhr.readyState === 4 && xhr.status === 200) {
        console.log(xhr.responseText);

        var list = xhr.responseText.split("\n")
        for(var i = 0; i <= list.length - 2; i++) {
            var arr = [list[i].split(",")[0], list[i].split(",")[1], list[i].split(",")[2]]
            this.directoryList.push(arr)
        }
      }
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.handleFilter(val, 10);
    },
    parseQuery() {
      const listQuery = {
        page: 1,
        pageSize: 10,
        sort: '+id'
      }
      this.listQuery = { ...listQuery, ...this.listQuery }
    },
    detail(row) {
      alert(row.id)
    }
  }
}
</script>

<style scoped>
  .hello {
    height: 95%;
    width: 100%;
    position: fixed;
    overflow: auto;
    background-image: linear-gradient(#c7edcc, #54FF9F);

  }

  .el-aside {
    border-color: blue;
  }

  .el-container {
    height: 100%;
  }

  .el-main {
    overflow-y:auto;
    margin-top: 0px;
    margin-left: 350px;
    height: 100%;
  }

  .el-row {
    margin-top: 5px;
  }

  .el-button {
    width: 60px;
    margin-left: 0px;
  }

  #searchButton {
    width: 100px;
    margin-left: 10px;
  }

  .detail-container {
    border: 0px solid blue;
    border-radius: 30px;
    width: 900px;
  }

  .el-table {
      border: 0px solid blue;
      border-radius: 25px;
      width: 900px;

  }

  .el-row {
    margin-top: 20px;
  }

  .el-input {
    border-radius: 10px;
  }


  .el-table .warning-row {
    background: #f0f9eb;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }

  .el-link {
    width: 150px;
    margin-left: 10px;

    text-decoration: none;
    color: black;
    font-size: 130%;
    #font-weight: bold;
  }
</style>



