<template>
  <el-container style="height: 100%; border: 0px solid #eee">
    <el-main>

      <el-input
        v-model="searchWord" placeholder="" style="width: 350px" class="filter-item"
        @clear="filter"  @keyup.enter.native="filter"  @click="filter"
      />
      <el-button id = 'searchButton'
        v-waves class="filter-item"
        type="primary" icon="el-icon-search" style="margin-left: 10px"
        @click="filter"
      >查询
      </el-button>

      <br/> <br/> <br/>

      <el-table
        :data="tableData"
        :show-header="false"
        fit
        highlight-current-row
        style="width: 900px; margin:0 auto;"

        :row-class-name="tableRowClassName"
        :row-style="{height: '0'}"
        :cell-style="{padding: '10px'}"
        >
        <el-table-column prop="id" label="id" min-width="30" align="center"></el-table-column>
        <el-table-column prop="category" label="姓名" min-width="100" align="center"></el-table-column>
        <el-table-column prop="mark" label="mark" min-width="50" align="center"></el-table-column>
        <el-table-column prop="title" label="title" min-width="250" align="left" :show-overflow-tooltip="true">
        </el-table-column>
        <el-table-column
          label="操作"
          width=""
          min-width="60"
          align="center"
        >
          <template slot-scope="{ row }">
            <el-button size="mini"  align="center"
                :type="getButtonType(row)"
                @click="readDetail(row)">detail</el-button>
          </template>
        </el-table-column>
      </el-table>

      <h1></h1>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        :current-page.sync="listQuery.page"
        :page-size="10"
        @current-change="handleCurrentChange"
        >
      </el-pagination>
    </el-main>

  </el-container>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [],

        metaInfo: [],
        total: 0,
        listQuery: {},
        searchWord: "",
        pageSize: 10,

        dir: ["the_big_bang_theory", "ted", "how_i_met_your_mother", "friends", "other", "topic", ],
        dict: {
          "the_big_bang_theory": "the big bang theory",
          "how_i_met_your_mother": "how I met your mother",
          "friends": "friends",
          "other": "其他",
          "ted": "ted",
          "topic": "topic"
        }
      }
    },
    mounted() {
      this.getMeta()
      this.filter(1, 10)
    },
    methods: {
      async getMeta() {
        this.metaInfo = []
        this.tableData = []
        var c = 0;

        for(var j = 0; j <= this.dir.length - 1; j++) {
           var dir = this.dir[j]
           // 同步请求
           const xhr = new XMLHttpRequest();
           xhr.open('GET', '/static/transcript/' + dir + '/meta', false);
           xhr.send(null);

           if(xhr.readyState === 4 && xhr.status === 200) {
             var list = xhr.responseText.split("\n")
             for(var i = 0; i <= list.length - 2; i++) {
                 c++;
                 var arr = [c, this.dict[dir], list[i].split(",")[0], list[i].split(",")[1], dir]
                 this.metaInfo.push(arr);
             }
           }

        }

      },
      async filter(pageNum, pageSize) {
        pageNum = this.listQuery.page
        pageSize = this.pageSize
        if(pageNum == NaN) {
          pageNum = 1;
          pageSize = this.pageSize;
        }

        var metaInfo = this.metaInfo
        var start = (pageNum - 1) * pageSize
        var end = start + pageSize - 1
        if(end >= metaInfo.length - 1) {
          end = metaInfo.length - 1
        }

        // 根据搜索词过滤
        var searchWord = this.searchWord
        searchWord = searchWord.replace(/^\s*|\s*$/g,"");
        var meta = new Array();
        for(var i = 0; i <= metaInfo.length - 1; i++) {
            var line = metaInfo[i][1] + metaInfo[i][2] + metaInfo[i][3] + metaInfo[i][4]
            if(line.search(searchWord) > -1) {
              meta.push(metaInfo[i])
            }
        }

        var list = new Array()
        for(var i = start; i <= end; i++) {
          if(i > meta.length - 1) break;
          list.push({id: meta[i][0], category: meta[i][1], mark: meta[i][2], title: meta[i][3], dir: meta[i][4] })
        }
        const currentCount = list.length

        list.sort(
          (pre, cur) =>
            pre.id - cur.id
        )
        this.tableData = list
        this.total = meta.length
        this.listLoading = false
      },
      handleCurrentChange(val) {
        this.filter(val, 10);
      },
      getButtonType(row) {
        var info = ['primary', 'success', 'danger', 'info', 'warning'];
        var index = row.id;
        var mod = index % 3;
        return info[mod];
      },
      getButtonType2(row) {
        var index = row.id;
        var mod = index % 5;
        if(mod === 0) {
          return 'primary';
        } else if(mod === 1) {
          return 'success';
        } else if(mod === 2) {
          return 'info';
        } else if(mod === 3) {
          return 'warning';
        } else if(mod === 4) {
          return 'danger';
        }
        return '';
      },
      readDetail(row) {

        var dir = `transcriptDetail/${row.dir}/${row.mark}`
        const routeData = this.$router.resolve({
              name: dir
            });
        window.open(routeData.href + dir, '_blank');

      },
      tableRowClassName ({ row, rowIndex }) {
          row.rowIndex = rowIndex;
      },
    },
  }
</script>

<style scoped>
  .el-container {
    height: 100%;
    width: 100%;
    position: fixed;
    overflow: auto;
    background-image: linear-gradient(#c7edcc, #54FF9F);
  }

  .el-main {
    overflow-y:auto;
    margin-top: 0px;
    margin-left: 0px;
    height: 100%;
  }

  .el-table {
      border: 0px solid blue;
      border-radius: 25px;
      width: 900px;
      margin: 0 auto;
  }
</style>



