<template>
  <div class="hello">
      <div class="hello">
        <el-container style="height: 100%; border: 0px solid #eee">
          <el-aside width="400px" style="position: fixed">
            <h1 style="color: black; font-family: 楷体">{{ this.titleMap[this.dir2] }} - {{ title }}</h1>
            <el-button class="ellipsis" :type="getButtonType(i)" @click="handle(directoryList[i - 1][0]);updateTitle(i - 1);" v-for="i in directoryList.length">
               {{ directoryList[i - 1][1] }}
            </el-button>
          </el-aside>

          <el-container>
            <el-main>

                <div class="htmlContent" style="text-align: left; margin-left: 20px; overflow-y:auto; height: 100%" v-html="htmlContent">
                </div>
            </el-main>
          </el-container>
        </el-container>
      </div>
  </div>
</template>

<script>
import axios from 'axios';
import Clock from './Clock'
import Windmill from './Windmill';

export default {
  name: 'Word',
  components: {
    "clock": Clock,
    "windmill": Windmill
  },
  data () {
    return {
      htmlContent: "",
      dir: "",
      dir2: "",
      directoryList: [],
      titleMap : {
        "design-pattern": "设计模式",
        "algorithm": "算法导论",
        "interview": "面试题",
        "goldbach": "哥德巴赫猜想",
        "spring-security": "权限管理",
        "java": "Java",
        "nginx": "nginx",
        "jwt": "jwt",
        "redis": "redis",
        "python": "python",
        "mybatis-plus": "mybatis-plus",
        "docker": "docker",

        "leetcode": "leetcode",
        "leetcode2": "leetcode",
        "leetcode3": "leetcode",
        "leetcode4": "leetcode",
        "leetcode5": "leetcode",
        "leetcode6": "leetcode",
        "leetcode7": "leetcode",

        "springboot": "springboot",
        "nacos": "nacos",
        "openfeign": "openfeign",
        "gateway": "网关",
        "sentinel": "sentinel",
        "seata": "seata",
        "rabbitmq": "rabbitmq",
      },
      title : ""
    }
  },
  mounted() {
      var dir = this.$route.params.dir
      var dir2 = this.$route.params.dir2
      this.dir = dir;
      this.dir2 = dir2;
      this.getMeta(dir, dir2);
      this.handle(this.directoryList[0][0]);
      this.updateTitle(0);
  },
  watch : {
    $route() {
      var dir = this.$route.params.dir
      var dir2 = this.$route.params.dir2
      this.dir = dir;
      this.dir2 = dir2;
      this.getMeta(dir, dir2);
      this.handle(this.directoryList[0][0]);
      this.updateTitle(0);
    }
  },
  methods: {
    async getMeta(directory, directory2) {
      this.directoryList = []
      // 同步请求
      const xhr = new XMLHttpRequest();
      xhr.open('GET', '/static/' + directory + '/' + directory2 + '/meta', false);
      xhr.send(null);

      if(xhr.readyState === 4 && xhr.status === 200) {
        var list = xhr.responseText.split("\n")
        for(var i = 0; i <= list.length - 2; i++) {
            var arr = [list[i].split(",")[0], list[i].split(",")[1]]
            this.directoryList.push(arr)
        }
      }
    },
    async handle(i) {
        var dir = this.$route.params.dir
        var dir2 = this.$route.params.dir2
        await axios.get("/static/" + dir + '/' + dir2 + "/" +  i + ".md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    updateTitle(i) {
      this.title = this.directoryList[i][1]
    },
    getButtonType(index) {
      var mod = index % 5;
      if(mod === 0) {
        return 'primary';
      } else if(mod === 1) {
        return 'success';
      } else if(mod === 2) {

        return 'info';
      } else if(mod === 3) {
        return 'warning';
      } else if(mod === 4) {
        return 'danger';
      }
      return '';
    },

  }
}
</script>

<style scoped>
  .hello {
    background-color: #c7edcc;
    height: 95%;
    width: 100%;
    position: fixed;
  }

  .el-aside {
    border-color:#98bf21;
    line-height: 50px;
  }

  .el-container {
    height: 100%;
    background-color: #c7edcc;
  }

  .el-main {
    overflow-y:auto;
    margin-left: 400px;
    height: 100%;
  }

  .el-row {
    margin-top: 10px;
  }

  .el-button {
    width: 150px;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 10px;
    margin: 9px;
  }

  .clock {
    float: center;
  }

  .htmlContent {
    background-color: #c7edcc;
    height: 100%;
    font-size: 18px;
  }

  .el-header {
    position: fixed;
    background-color: #c7edcc;
    color: #333;
    text-align: left;
    line-height: 30px;
    width: 350px;
  }

  a {
    text-decoration: none;
    color: #dd00dd;
    font-size: 130%;
    #font-weight: bold;
  }

  .ellipsis {
    white-space: nowrap; /* 确保文本在一行内显示 */
    overflow: hidden; /* 隐藏溢出的内容 */
    text-overflow: ellipsis; /* 使用省略号表示溢出的文本 */
  }

</style>



