<template>
  <div class="hello">
      <div class="hello">
        <el-container style="height: 100%; border: 0px solid #eee">
          <el-aside width="350px" style="position: fixed">
            <p />
            <windmill />


            <el-tag type="success" style="width: 100%; height: 30px; font-size: 15px; margin-top: 30px;">
              跟着《老爸老妈的浪漫史》学英语
            </el-tag>

            <el-row>
              <el-button type="primary" circle @click="handle1_1">01-1</el-button>
              <el-button type="success" circle @click="handle1_2">01-2</el-button>
              <el-button type="info"    circle @click="handle1_3">01-3</el-button>
            </el-row>
            <el-row>
              <el-button type="warning" circle @click="handle3_1">03-1</el-button>
              <el-button type="danger"  circle @click="handle3_2">03-2</el-button>
              <el-button                circle @click="handle3_3">03-3</el-button>
            </el-row>
            <el-row>
              <el-button type="primary" circle @click="handle4_1">04-1</el-button>
              <el-button type="success" circle @click="handle4_2">04-2</el-button>
            </el-row>
          </el-aside>

          <el-container>
            <el-main>

              <div class="htmlContent" style="text-align: left; margin-left: 50px; overflow-y:auto; height: 100%" v-html="htmlContent">abc</div>
            </el-main>
          </el-container>
        </el-container>
      </div>
  </div>
</template>



<script>
import Clock from './Clock';
import Windmill from './Windmill';
import axios from 'axios';

export default {
  name: 'Script',
  components: {
        "clock": Clock,
        "windmill": Windmill,
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      htmlContent: ""
    }
  },
  mounted() {
    this.handle1_1()
  },
  methods: {
    async handle1_1() {
        await axios.get("/static/script/01_1.md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    async handle1_2() {
        await axios.get("/static/script/01_2.md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    async handle1_3() {
        await axios.get("/static/script/01_3.md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    async handle3_1() {
        await axios.get("/static/script/03_1.md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    async handle3_2() {
        await axios.get("/static/script/03_2.md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    async handle3_3() {
        await axios.get("/static/script/03_3.md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    async handle4_1() {
        await axios.get("/static/script/04_1.md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    async handle4_2() {
        await axios.get("/static/script/04_2.md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    }
  }
}
</script>

<style scoped>
  .hello {
    background-color: #c7edcc;
    height: 95%;
    width: 100%;
    position: fixed;
  }

  .el-aside {
    border-color:#98bf21;
    border-right-style:solid;
    line-height: 50px;
  }

  .el-container {
    height: 100%;
    background-color: #c7edcc;
  }

  .el-main {
    overflow-y:auto;
    margin-left: 350px;
    height: 100%;
  }

  .el-row {
    margin-top: 20px;
  }

  .el-button {
    margin-left: 18px;
  }

  .clock {
    float: center;
  }

  .htmlContent {
    background-color: #c7edcc;
    height: 150%;
    font-size: 18px;
    margin-left: 50px;
  }

  .el-header {
    position: fixed;
    background-color: #c7edcc;
    color: #333;
    text-align: left;
    line-height: 30px;
    width: 350px;
  }

  a {
    text-decoration: none;
    color: #dd00dd;
    font-size: 130%;
    #font-weight: bold;
  }

</style>



